@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #8D8D8D;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#drum-machine {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10vh;
  border: 3px solid #FFA500;
  font-size: 1.8em;
  font-family: 'Press Start 2P', cursive;
  background-color: #B3B3B3;

}

#display {
  margin: 20px;
  width: 220px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #808080;
  font-size: 1.2rem;
}

.row {
  display: flex;
  flex-direction: row;
  margin: 0 5px;
}

.bottom {
  margin-bottom: 5px;
}

.drum-pad {
  margin: 5px;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  box-shadow: 2px 2px 5px 1px black;
  background-color: #808080;
  cursor: pointer;
  user-select: none;
}
